<!--  -->
<template>
    <div class="conter" style="margin-bottom: 2.49rem">
        <div class="title">
            <span>热门预告片</span>
        </div>
        <div style="margin-top: 0.826667rem;">
            <swiper ref="mySwiper" :options="swiperOptions" >
                <swiper-slide class="per" v-for="item in list" :key="item.index">
                    <video :src="item.video[0]"></video>
                 </swiper-slide>
                <div class="swiper-pagination" slot="pagination"></div>
            </swiper>
        <div class="er">
            <p>即将上映</p>    
        </div>
        <div class="time">
            <span>2月3号</span>
            <span>2月4号</span>
            <span>2月5号</span>
            <span>2月6号</span>
            <span>2月7号</span>
        </div>
        <ul>
            <li v-for="item in anot" :key="item.index" @click="toMovie(item.mId)">
                <div class="lat">
                    <img :src="item.movie.small" alt="">
                </div>
                <div class="right">
                    <p>{{ item.movie.title }}</p>
                    <p>1.2万人想看</p>
                    <p>导演: {{ item.movie.director[0] }}</p>
                    <p>演员: {{ item.movie.scriptwriter[0] }}</p>
                </div>
                <div class="buy">
                    <p>购票</p>
                </div>
            </li>
        </ul>    
        </div>
    </div>
</template>

<script>
import axios from 'axios'
  export default {
    name: 'carrousel',
    data() {
      return {
        list:[],
        anot:[],
        swiperOptions: {
            slidesPerView: 1.4, // 同行展示几页呢
            spaceBetween: 90, //  每个页面之间的间距
            centeredSlides: false,
            loop:true,

        
      }
    }
    },
    computed: {
      swiper() {
        return this.$refs.mySwiper.$swiper
      }
    },
    mounted() {
      console.log('Current Swiper instance object', this.swiper)
      this.swiper.slideTo(3, 1000, false)
    },
    created() {
        this.$store.state.currentPage = this.$router.currentRoute.fullPath
        console.log(this.$store.state.currentPage)
        axios.get(`http://bufantec.com/api/douban/movie/selectPreview`).then((res) => {
            // console.log(res);
            this.list=res.data.data
            // console.log(this.list);
            
        }),
        axios.get(`http://bufantec.com/api/douban/movie/selectPreview/coming_soon`).then((resson) => {
            // console.log(resson);
            this.anot=resson.data.data
            console.log(this.anot);

        })
    },
    methods: {
        toMovie(mid) {
            this.$router.push('/detail/' + mid)
        }
    }
  }

</script>

<style lang='scss' scoped>
.conter{
    width: 670px;
    height: 100%;
    margin: 0 auto;
    
}
.swiper-slide{
    width: 636px;
    height: 100%;
    border: 1px solid red;
    
}
video{
    width: 100%;
    height: 100%;
}
.title{
    width: 180px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    font-size: 36px;
    color: #DFDFDF;
}
.er{
    width: 144px;
    height: 50px;
    margin-top:44px;
}
.er p{
        font-size: 36px;
        color: #DFDFDF;
    }
.time{
    display: flex;
    margin-top:38px;

}
.time span{
    
    width: 164px;
    height: 52px;
    line-height: 52px;
    text-align: center;
    font-size: 28px;
    color: #DFDFDF;
    border: 4px solid #979797;
    border-radius: 26px;
}
ul{
    
    margin-top: 44px;
}
li{
    position: relative;
    display: flex;
    align-items:center;
    width: 100%;
    margin-top: 48px;
    height: 280px;
    background: #33363D;
    border-radius: 12px;
}
li img{
    width: 234px;
    height: 280px;
}
.right{
    padding-left: 28px;
}
.right p:nth-child(1){
    position: absolute;
    top:26px;
    font-size: 36px;
    color: #FFFFFF;
}
.right p:nth-child(2){
    position: absolute;
    top:96px;
    font-size: 30px;
    color: #FBC34A;
}
.right p:nth-child(3){
    position: absolute;
    top: 154px;
}
.right p:nth-child(4){
    position: absolute;
    top: 192px;
}

.right p:nth-child(3),
.right p:nth-child(4){
    
    font-size: 24px;
    color: #FFFFFF;
    opacity: 0.47;
}
.buy{
    position: absolute;
    top: 124px;
    left: 540px;
    width: 108px;
    height: 72px;
    line-height: 72px;
    text-align: center;
    background-image: linear-gradient(150deg, #624BA3 0%, #CB4498 100%);
    box-shadow: 0 0 8px 2px rgba(242,109,125,0.18);
    border-radius: 12px;
    border-radius: 12px;
}
.buy p{
    
    font-size: 24px;
    color: #FFFFFF;
}
</style>
